import React,{useState} from 'react'
import {Container,BlockHeader} from '../common'
import {useStaticQuery,graphql} from 'gatsby'
import {injectIntl,FormattedMessage} from 'gatsby-plugin-intl'
const Block1 = ({intl}) => {
    const data = useStaticQuery(graphql`
        query Terms {
            allMarkdownRemark(filter: {frontmatter: {tags: {regex: "/terms/"}}}, sort: {fields: frontmatter___tags}) {
                nodes {
                frontmatter {
                    title
                    tags
                }
                html
                }
            }
        }
    `)
    console.log(data)
    return (
        <>
            <BlockHeader title={intl.formatMessage({id: "terms.title",defaultMessage: "Terms and Condition"})}/>
            <Container classNames="bg-lights-white">
                <div className="px-4 py-4 shadow-lg mb-8 bg-white">
                    <div className="font-sans font-bold text-16 text-lights-bluebold uppercase mb-4"><span className="text-lights-orangebold">Terms of</span> service</div>
                    <div className="mb-8">
                        <div className="terms-item" dangerouslySetInnerHTML={{__html: data.allMarkdownRemark.nodes[0].html}} />
                    </div>
                    <div className="font-sans font-bold text-16 text-lights-bluebold uppercase mb-4"><span className="text-lights-orangebold">Declaration of exemption</span> from liability</div>
                    <div className="mb-8">
                        <div className="terms-item" dangerouslySetInnerHTML={{__html: data.allMarkdownRemark.nodes[1].html}} />
                    </div>
                    <div className="font-sans font-bold text-16 text-lights-bluebold uppercase mb-4"><span className="text-lights-orangebold">CONTACT</span> INFORMATION</div>
                    <div className="">
                        <div className="terms-item" dangerouslySetInnerHTML={{__html: data.allMarkdownRemark.nodes[2].html}} />
                    </div>
                </div>
            </Container>
        </>
    )
}
export default injectIntl(Block1)